import React from "react"
import MarkdownIt from "markdown-it"
import type { Token } from "markdown-it"
import { full as emoji } from 'markdown-it-emoji'
import markdownItFootnote from 'markdown-it-footnote'

const md = new MarkdownIt({
  html: true,
  linkify: true,
  breaks: true,
  typographer: true,
})

// Import and use plugins
md.use(require("markdown-it-mark"))
md.use(require("markdown-it-ins"))
md.use(markdownItFootnote)
md.use(emoji)
md.use(require("markdown-it-abbr"))
md.use(require("markdown-it-deflist"))
md.use(require("markdown-it-sub"))
md.use(require("markdown-it-sup"))

// Add target="_blank" to external links
md.renderer.rules.link_open = function (tokens: Token[], idx: number, options: any, env: any, self: any) {
  const aIndex = tokens[idx].attrIndex("target")
  if (aIndex < 0) {
    tokens[idx].attrPush(["target", "_blank"])
  } else if (tokens[idx].attrs) {
    tokens[idx].attrs[aIndex][1] = "_blank"
  }
  return self.renderToken(tokens, idx, options)
}

interface MDRenderProps {
  md: string
}

export const MDRender: React.FC<MDRenderProps> = ({ md: content }) => {
  const result = md.render(content)
  return <div dangerouslySetInnerHTML={{ __html: result }} />
}
